<template>
    <div class="ma-5">
        <v-data-table :items="blogArticles" :loading="loading" :page.sync="currentPage" :search="search"
                      :headers="headers" sort-by="id" class="elevation-2" hide-default-footer :items-per-page="10">
            <template v-slot:top>
                <v-toolbar flat>
                    <v-toolbar-title>文章列表</v-toolbar-title>
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="search" append-icon="mdi-magnify" label="搜索" clearable single-line
                                  hide-details/>
                    <v-btn color="primary" :disabled="loading" icon class="mb-2 me-2" @click="refresh">
                        <v-progress-circular color="primary" size="18" width="2" v-if="loading" indeterminate/>
                        <v-icon v-else>mdi-refresh</v-icon>
                    </v-btn>
                    <v-btn color="primary" dark class="mb-2" replace :to="{path:'/admin/article/newArticle'}">新增</v-btn>
                </v-toolbar>
            </template>

            <template v-slot:item.id="{ item }">
                <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
                <strong>{{item.id}}</strong>
            </template>

            <template v-slot:item.title="{ item }">
                <ArticlePreviewFullScreenDialog :blog-article="item"/>
            </template>

            <template v-slot:item.typeString="{ item }">
                <v-btn text>{{item.typeString}}</v-btn>
            </template>

            <template v-slot:item.tags="{ item }">
                <v-chip-group>
                    <v-chip v-for="tag in item.tags" :key="tag">{{tag}}</v-chip>
                </v-chip-group>
            </template>
            <template v-slot:item.original="{ item }">
                <v-simple-checkbox v-model="item.original" disabled/>
            </template>
            <template v-slot:item.canReward="{ item }">
                <v-simple-checkbox v-model="item.canReward" disabled/>
            </template>
            <template v-slot:item.canComment="{ item }">
                <v-simple-checkbox v-model="item.canComment" disabled/>
            </template>
            <template v-slot:item.show="{ item }">
                <v-simple-checkbox v-model="item.show" disabled/>
            </template>
            <template v-slot:item.deleted="{ item }">
                <v-btn icon small @click="deleteOrRestoreItem(item)" :color="item.deleted?'primary':'warning'">
                    <v-icon>
                        {{item.deleted?"mdi-restore":"mdi-delete"}}
                    </v-icon>
                </v-btn>
            </template>
        </v-data-table>
        <v-pagination class="mt-5" v-model="currentPage" :length="totalPages"/>
    </div>
</template>

<script>
    import ArticlePreviewFullScreenDialog from "@/components/admin/article/ArticlePreviewFullScreenDialog";

    export default {
        name: "ArticleList",
        components: {ArticlePreviewFullScreenDialog},
        data() {
            return {
                blogArticles: [],
                currentPage: 1,
                totalPages: 0,
                loading: true,
                search: "",

                headers: [
                    {text: '序号', align: 'center', value: 'id'},
                    {text: '标题', value: 'title'},
                    {text: '点击次数', value: 'clickCount'},
                    {text: '发布时间', value: 'uploadTimeString'},
                    {text: '更新时间', value: 'updateTimeString'},
                    {text: '分类', value: 'typeString'},
                    {text: '标签', value: 'tags', sortable: false},
                    {text: '是否为原创', value: 'original'},
                    {text: '是否可以打赏', value: 'canReward'},
                    {text: '是否可以评论', value: 'canComment'},
                    {text: '是否展示在网站上', value: 'show'},
                    {text: '删除或恢复', value: 'deleted'},
                ],
            }
        },
        computed: {},
        watch: {
            currentPage(currentPage) {
                this.$vuetify.goTo(0)
                this.getArticles(currentPage)
            }
        },
        methods: {
            editItem(item) {
                this.$router.push({
                    name: "/admin/article/newArticle",
                    params: {
                        article: item
                    }
                })
            },
            deleteOrRestoreItem(item) {
                let deleted = !item.deleted
                let msg = deleted ? "删除" : "恢复"
                if (confirm("确定" + msg + "：\n" + item.title)) {
                    let index = this.blogArticles.indexOf(item)
                    let _this = this
                    this.axios.delete(this.API.URL.DELETE_ARTICLE + item.id + "&deleted=" + deleted)
                        .then(function (result) {
                                let data = result.data
                                if (data.success) {
                                    _this.blogArticles[index].deleted = deleted
                                    alert(msg + "成功")
                                }
                            }, () => alert(msg + "失败")
                        )
                }
            },
            refresh() {
                this.currentPage = 1
                this.getArticles(this.currentPage)
            },
            getArticles(currentPage) {
                this.loading = true
                let _this = this
                this.axios.get(this.API.URL.QUERY_ALL_WITH_ALL_ATTRS + "&page=" + (currentPage - 1))
                    .then(function (result) {
                        _this.loading = false
                        let data = result.data
                        if (data.success) {
                            _this.totalPages = data.data.totalPages
                            _this.blogArticles = data.data.data
                            // console.log("_this.tags", _this.tags)
                        }
                    }, () => {
                        _this.loading = false
                    })
            }
        },
        created() {
            this.getArticles(1)
        }
    }
</script>

<style scoped>

</style>